
.container{
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.path {
    fill: none;
    stroke: #f6f4f4;
    stroke-width: 1px;
    stroke-dasharray: 200px;
}
.spacer-area{
    width: 100%;
    height: 80vh;
}
.spacer{
    width: 300px;
    height: 50vh;
    background-color: 222;
}

.svg{
    width:  100%;
    height: 100%;
}

